.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #342a28;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


li{
  list-style: none ;
}

ul{
  margin:0;
  padding:0;
}


.profile_name{
  margin:0;
  font-size:20px
}

 @media screen and (max-width: 700px){
  .profile{
    display: none
  }

  }

 